const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()
export default {
	name: '',
	data() {
		return {
			list: [],
			keyword: '',
			params: {
				category_id: "",
				kw: "",
				sort_type: 1,   //1上架时间 2价格 3评论 4销量
				sort_val: 1,    // 0升序 1降序
			}
		}
	},
	//初始化
	onLoad() {
		//检测是否有url参数
		let pages = getCurrentPages();
		let { options } = pages.pop();
		if(options.cid) {
			this.params.category_id = options.cid;
			this.getList()
		}else if(options.keyword) {
			this.params.kw = options.keyword;
			this.getList()
		}else {
			this.getList();
		}	
	},
	// 页面显示
	onShow() {
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/home/home?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		
		//获取商品列表
		getList() {
			wx.showLoading({title: '加载中...'})
			util.request(api.getGoodsList,this.params,'post').then(res=>{
				wx.hideLoading()
				if(res.code === 0) {
					this.list = res.data.list;
				}
			})
		},
		//筛选点击
		searchlist(type) {
			this.params.sort_type = type;
			this.getList()
		},
		searchsort() {
			let sort = this.params.sort_val;
			this.params.sort_val = sort == 1 ? 0 : 1;
			this.getList()
		},
		dealPrcture(str) {
			if(!str) return ;
			let newStr = str.split(',');
			return newStr[0]
		},
		//input 聚焦
		searchInt(e) {
			this.params.kw = e.detail.value;
		},
		searchConfirm(e) {
			this.params.kw = e.detail.value;
			this.getList()
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
	}
}
